@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1 {
  font-family: 'Source Code Pro', monospace;
  text-align: center;
  font-size: 3.5em;
  text-align:center;
  font-weight: bold;
  font-style: italic;
}
.cardImg {
  max-height: 185px;
  overflow: hidden;
}
.col-md-6, .col-lg-4 {
  display: flex;
}
.card {
  margin-bottom: 20px;
}
.card a {
  position: absolute;
  bottom: 5px;
  /*left: 20px;
   visibility: hidden; */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.card-body {
  padding-bottom: 40px;
}
.card:hover {
  background: blue;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  color: #fff;
  opacity: 0.6;
}

.row.topToggle {
  flex-direction: row;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
@media(min-width: 789px) {
form {
  flex: 0 0 30%;
}
div.input-group {
  margin-bottom: 1rem!important;
  flex: 0 0 40%;
  /* margin-left: 20px; */
}
}
.pagenumber {
  padding: 10px;
  border: 1px solid;
  width: 75px;
  text-align: center;
  margin-left: 10px;
  cursor: pointer;
  display: inline-block;
}

.pagenumber:first-child {
  margin-left: 0;
}

.pagenumber:hover {
  background: #000;
  color: #fff;
}
.pagenumber:hover {
  background: #000;
  color: #fff;
}
.pageHolder {
  display: block;
  clear: both;
  /* width: 100%; */
  flex: 1 1 100%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
h1 {
  font-family: 'Source Code Pro', monospace;
  text-align: center;
  font-size: 3.5em;
  text-align:center;
  font-weight: bold;
  font-style: italic;
}
.pagenumber.selected {
  background: #000;
  color: #fff;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

